@font-face {
  font-family: 'Open Sans';
  font-weight: 400;
  src: local('Open Sans'), url(./fonts/gilroy-regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-weight: 700;
  src: local('Open Sans'), url(./fonts/gilroy-bold.ttf) format('truetype');
}
.App{
  margin-left: 300px;
  margin-right: 300px;
  margin-top: 40px;
  font-family: 'Gilroy';
}
.menu{
  position: absolute;
  top:30px;
  font-family: 'Gilroy';
  font-weight: bold;
  font-size: 20px;
  right: 300px;
  display: flex;
  justify-content: space-between;
  width: 900px;
  padding-left: 10px;
}
.headerButton{
  position: relative;
  top: -13px;
  right: 0px;
  font-family: 'Gilroy';
  font-weight: bold;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: center;
  color: white;
  text-align: center;
  width: 236px;
  height: 49px;
  background: #367BF5;
  border-radius: 50px;
  cursor: pointer;
}
.headerWrapper{
  display: flex;
  margin-top: 26px;
  justify-content: space-between;
  width: 100%;
  margin-left: 300px;
}
.logo{
  margin-top: -8px;
}
.header{
  z-index: 100;
  height: 90px;
  background: white;
  position: fixed;
  top:0;
  left: 0;

  width: 100%;
}
.footer{
  position: relative;
  background: #367BF5;
  width: 100%;
  height: 670px;
}
.kopa{
  display: flex;
  width: max-content;
}
.bgFoo{
  z-index: -1;
  position: absolute;
  bottom: 350px;
  //top: -200px
}

.huey{
  background: #367BF5;
  height: 25px;
  width: 25px;
  margin-top: -8px;
  margin-right: 15px;
  border: 2px solid white;

}
.huey, .radioText{
  cursor: pointer;
}
.radioLine{
  display: flex;
  //justify-content: center;
  align-items: center;

}

.footerText{
  color: white;
  font-family: 'Gilroy';
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 55px;
  margin-top: -18px;
  margin-right: 140px;
  width: 530px;
  height: max-content;
}
.radioText{
  font-size: 20px;
  font-family: Gilroy;
  font-weight: normal;
  color: white;
}
.tag{
  padding-top: 40px;
  color:white;
  //font-family: 'Gilroy Black';
  font-family: Gilroy;
  font-weight: bold;
  font-size: 64px;
  text-align: center;
}
.tagb{
  padding-top: 40px;
  color:#3F3D56;
  //font-family: 'Gilroy Black';
  font-family: Gilroy;
  font-weight: bold;
  font-size: 64px;
  text-align: center;
  margin-bottom: 110px;
}
.footerWrapper{
  padding-top: 4%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-left: 300px;
  margin-right: 300px;
}
.footerMail{
  width: 630px;
  height: 200px;
}
.active{
  border-bottom: 5px solid #367BF5;
  margin-bottom: 14px;

}
NavLink{
  text-decoration: none;
  font-size: 16px;
  margin-right: 8px;
  padding-left: 8px;
}
a{
  color: #3F3D56;
  text-decoration: none;
}
.hrefToPerson{
  padding-bottom: 1px;
  border-bottom: 1px solid black;
}
.arrowTop{
  cursor: pointer;
  position: fixed;
  right: 60px;
  bottom: 100px;
  opacity: 0;
  transition: opacity ease-in-out .3s;
}
.contact-form{
  //display: flex;
  //flex-direction: column;
  margin-top: 17px;
}
.leftSideFoo{
  display: flex;
  flex-direction: column;
  width: 630px;
}
.topBarEmail{
  width: 91%;
  display: flex;
  justify-content: space-between;
}
.blockEmail{
  display: flex;
  flex-direction: column;
}
label{
  font-size: 14px;
  color: #a5a5a5;
  padding-bottom: 7px;
}
.inputEmail{
  margin-bottom: 30px;
  width: 109%;
  height: 34px;
  border-style: solid;
  border-width: 2px;
  border-color: white;
  border-radius: 50px;
  background-color: transparent;
  font-family: 'Gilroy';
  line-height: 1.42857143;
  padding-left: 16px;
  padding-right: 12px;
  padding-bottom: 8px;
  padding-top: 8px;
  color: white;
  outline: none;
  font-size: 20px;
  font-weight: 400;
}
::placeholder{
  //color: hsla(0, 0%, 100%, 0.25);;
  color: white ;
}
.message{
  margin-top: -6px;
  padding-left: 16px;
  padding-right: 12px;
  padding-bottom: 8px;
  padding-top: 8px;
  margin-bottom: 30px;
  height: 100px;
  font-family: "Gilroy";
  border-style: solid;
  border-width: 2px;
  border-color: white;
  border-radius: 30px;
  background-color: transparent;
  line-height: 1.42857143;
  color: #fff;
  outline: none;
  font-size: 20px;
  font-weight: 400;
  resize: none;
}
.sendEmail{
  padding-left: 22px;
  padding-right: 22px;
  font-family: "Gilroy";
  margin-top: -6px;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  vertical-align: center;
  color: #367BF5;
  background: white;
  border-radius: 30px;
  width: 100%;
  height: 54px;
  cursor: pointer;
  outline: none;
  border: transparent;
}
.activeSend{
  display: none;
}
.afterSend{
  display: none;
  position: absolute;
  top:0;
  left: 670px;
  border-style: solid;
  border-width: 2px;
  border-radius: 30px;
  background-color: #367BF5;
  height: 310px;
  width: 100%;
  font-family: Gilroy;
  font-size: 20px;
  color: white;
  font-weight: normal;
  text-align: center;
}
.emailWrapper{
  position: relative;
}
.afterSendText{
  margin-top: 8%;
}
.chek{
  margin-top: 8%;
}
.topStText{
  margin-bottom: 3px;
}
.compInfo{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 75px;
  margin-top: -20px;
}
.botFooter{
  align-items: center;
  width: 68.5%;
  display: flex;
  justify-content: space-between;
  color: white;
  margin-top: 200px;
  margin-left: 300px;
  margin-right: 100px;
  font-family: Gilroy;
  font-weight: normal;
  font-size: 20px;
}
.icons{
  //width: max-content;
  height: max-content;
  display: flex;
  align-items: flex-end;
  margin-top: 15px;
}
.iconTelegram{
  fill:grey;
  transition: fill ease-in-out .25s;
}
.telegram{
  padding-left: 25px;
}
.telegram:hover{
  cursor: pointer;
  .iconTelegram{
    fill:white;
  }
}
.topBarProjects{
  color: black;
  width: 100%;
  display: flex;
  margin-top: -30px;
  justify-content: space-between;
  height: max-content;
}
.next{
  position: absolute;
  left: 1300px;
  top:150px;
  font-size: 20px;
  color: #9c9c9c;
  width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.spaceTagPage{
  margin-top: 150px;
}
.spaceBotPaper{
  margin-bottom: 200px;
}
.spacePhotoCon{
  margin-top: 50px;
}
.conMain{
  display: flex;
  justify-content: space-between;
}
.conRight{
    width: 580px;
}
.conRightBot{
  display: flex;
  justify-content: space-between;
}
.conRightBotRight{
  margin-left: -20px;
}
.socialConActivity{
  padding-top: 10px;
}
.ic{
  margin-right: 20px;
  cursor: pointer;
}
.textAbout{
  width: 570px;
  font-family: Gilroy;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 29px;
  margin-bottom: 8px;

  color: #94A2B3;
}
.alignAbout{
  display: flex;
  align-items: center;
}
.tagAbout{
  width: 350px;
}
.edAbout{
  margin-top: 10px;
  text-align: left;
  font-size: 20px;
  color: #94A2B3;
  font-family: Gilroy;
  font-weight: normal;
  width: 280px;
}
.edAbout2{
  width: 320px;
}
.edAbout3{
  width: 300px;
}

@media (max-width: 780px)  {
  .bgHome, .delMob{display: none}
  .App{ margin-left: 35px; margin-right: 35px;}
  .headerWrapper{margin-left: -1px; transform: scale(0.83)}
  .menu{margin-top: -55px; margin-left: 300px; max-width: 700px; position: static; padding-right: 30px}
  .header{overflow-y: auto; max-width: 80vh; height:  90px}
  //.html, .wrapper{overflow-x: hidden}
  .arrowTop{ transform: scale(0)}
  .footer, .botFooter, .emailWrapper, .contact-form, .kopa, .topBarEmail,.header{display: block}
  .footerMail{margin-left: -270px; width: 40vh}
  .botFooter, {margin-left: 30px; background-color: #367BF5; padding-bottom: 30px; margin-top: 230px}
  .tag{font-size: 33px; margin-bottom: 20px;}
  .topBarEmail, {width: 32.2vh}
  .message{ width: 35.2vh;margin-top: 0px}
  .sendEmail{width: 39vh}
  .footerMail{margin-bottom: 150px}
  .leftSideFoo{ margin-top: 10px; width: 30vh}
  .kopa{width: 30vh}
  .radioLine{ margin-top: -10px; width: 35vh}
  .vhMob, .footerText{width: 30vh}
  .footer{margin-bottom: 80px;width: 46vh}
  .blueMob{background-color: #367BF5}
  .tagb{font-size:33px;text-align:center;margin-bottom:60px;margin-top:40px;margin-left:0;}
  .spaceTagPage{margin-top: 70px}
  .stBlock, .lineBlocks{width: 30vh}
  .textAbout{width: 41.3vh; text-align: left;margin-left: 125px}
  .photoAuthor{transform: scale(0.75)}
  .conLeft{margin-top: -40px;margin-bottom: -20px}
  .spaceBotPaper{margin-bottom: -45px}
  .imgMob{transform: scale(0.81);margin-left: -32px}
  .tag2{width: 40vh}
  .edAbout2{width: 36vh}
  .spaceBB{padding-bottom: 60px}
  .spaceBV{margin-bottom: 30px}
  .verh{margin-top: -30px}
  .aboutImg{transform: scale(0.53); margin-left: -135px; margin-top: -100px}
  .textCon{width: 37vh; text-align: left;  margin-left: 1px};
  .textAbout{margin-left: -15px; width: 38vh}
  .conRightBot{flex-direction: column;margin-bottom: 80px;margin-top: -60px; justify-content: center; margin-left: 80px}
.socialConActivity, .conRightBotRight, .conRightBotLeft, .conRightTop, .conRight,.textCon{width: 35vh}
.conRightBotLeft,.textCon,.socialConActivity{width: 30vh}
}

